<script>
import Setup from './Setup.vue';

export default {
    name: "Emails",
    components: {
        Setup
    },
    data() {
        return{
            error: "",
            loading: false,
            redirectUrl: "",
            WorkEmail: "",
            HomeEmail: ""
        }
    },
    methods: {
        handleFormSubmit(){
            let data = {
                redirectUrl: this.redirectUrl,
                HomeEmail: this.HomeEmail,
                WorkEmail: this.WorkEmail
            };
            this.error = "";

            if(typeof data.WorkEmail != "undefined" && data.WorkEmail.length && !this.$validateEmail(data.WorkEmail)){
                this.error = "Work email is not valid.";
            }else if(typeof data.HomeEmail != "undefined" && data.HomeEmail.length && !this.$validateEmail(data.HomeEmail)){
                this.error = "Personal email is not valid.";
            }

            if(this.error != "") return;

            this.loading = true;

            this.$ajax.post('/Setup/Emails', data)
            .then(resp => {
                if (typeof resp.data != "undefined"){
                    if(resp.data.Error != null) {
                        this.error = resp.data.Error;
                    }else if(resp.data.ClientRedirect && resp.data.RedirectUrl.length){
                        window.location.href = resp.data.RedirectUrl;
                    }else if(resp.data.RedirectPage != null){
                        var queryString = resp.data.RedirectUrl != null ? {redirectUrl: resp.data.RedirectUrl} : {}
                        this.$router.push({name: resp.data.RedirectPage, query: queryString});
                    }
                }
            })
            .catch(() => {})
            .then(() => {
                this.loading = false;
            });
        }
    },

    mounted() {
        // this.$reformatUrl("Emails");
        if (typeof this.$route.query != "undefined" && typeof this.$route.query.redirectUrl != "undefined") {
            this.redirectUrl = this.$route.query.redirectUrl;
        }
    }
}
</script>

<template>
    <setup title="Setup Your Email" :error="error">
        <form method="post" @submit.prevent="handleFormSubmit">
            <div class="default-form w-border" style="text-align: left; margin: auto;">
                <div class="form-row">
                    <input type="text" placeholder="Enter Work Email (optional)" v-model="WorkEmail" class = "long" />
                </div>
                <div class="form-row">
                    <input type="text" placeholder="Enter Personal Email (optional)" v-model="HomeEmail" class = "long" />
                </div>
                <div class="form-row" style="text-align: center;">
                    <div class="spinner small light" v-show="loading"></div>
                    <input type="submit" value="Continue" class="button large blue" v-show="!loading" v-cloak />
                </div>
            </div>
        </form>
    </setup>
</template>
