<script>
import Setup from './Setup.vue'
export default {
    name: "Reset",
    data() {
        return {
            error: "",
            loading: false,
            userId: "",
            userName: "",
            redirectUrl: "",
            email: "",
            recoveryToken: "",
            setPassToken: "",
            companyName: "",
            firstName: "",
            lastName: "",
            questions: [],
            pageSubtitle: ""
        }
    },
    components: {
        Setup,
    },
    methods: {
        handleFormSubmit(){
            let data = {
                questions: this.questions,
                userName: this.userName,
                email: this.email,
                token: this.recoveryToken,
                companyName: this.companyName,
                firstName: this.firstName,
                lastName: this.lastName,
                userId: this.userId
            }
            this.error = "";
            let valid = true;
            for (var i = 0; i < data.questions.length; i++) {
                if(data.questions[i].hasOwnProperty("AnswerText") && data.questions[i].AnswerText == null){
                    valid = false;
                    break;
                }
            }
            if(!valid){
                this.error = "All answers must be filled out.";
                return;
            }
            this.loading = true;

            this.$ajax.post('/Password/Reset', data)
            .then(resp => {
                if(resp.data.Error != null) {
                    this.error = resp.data.Error;
                }else if(resp.data.RedirectPage){
                    this.setPassToken = resp.data.Token;
                    this.$router.push({name: resp.data.RedirectPage, params: {setPassToken: this.setPassToken,
                                                                                userId: this.userId,
                                                                                pageSubtitle: this.pageSubtitle
                                                                            },
                                                                    query: {redirectUrl: this.redirectUrl}});
                }
            })
            .catch(() => {})
            .then(() => {
                this.loading = false;
            });
        },
        populatePage() {
            this.loading = true;
            this.$ajax.get('/Password/Reset')
            .then(resp => {
                if (typeof resp.data != "undefined"){
                    if(resp.data.Error != null) {
                        this.error = resp.data.Error;
                    }else{
                        this.questions = resp.data.Questions;
                        this.userName = resp.data.UserName;
                        this.email = resp.data.Email;
                        this.recoveryToken = resp.data.Token;
                        this.companyName = resp.data.CompanyName;
                        this.firstName = resp.data.FirstName;
                        this.lastName = resp.data.LastName;
                        this.userId = resp.data.UserId;
                        this.pageSubtitle = this.companyName + " / " + this.firstName + " " + this.lastName;
                    }
                }
            })
            .catch(() => {})
            .then(() => {
                this.loading = false;
            });
        }
    },
    mounted() {
        // this.$reformatUrl("Reset");
        this.populatePage();
        if (typeof this.$route.query != "undefined"){
            if(typeof this.$route.query.redirectUrl != "undefined") {
                this.redirectUrl = this.$route.query.redirectUrl;
            }
        }
    }
}
</script>

<template>
    <setup title="Answer Security Questions" :error="error">
        <form method="post" @submit.prevent="handleFormSubmit">
            <h3>{{pageSubtitle}}</h3>
            <div class="default-form w-border" style="text-align: left; margin: auto;">
                <br />
                <span v-for="question in questions" v-bind:key="question">
                    <div class="form-row">
                        {{question.Text}}
                    </div>
                    <div class="form-row">
                        <input type="text" placeholder="Answer the Question" autocomplete = "off" v-model="question.AnswerText" class = "long" />
                    </div>
                    <br />
                </span>
                <div class="form-row" style="text-align: center;">
                    <div class="spinner small light" v-show="loading"></div>
                    <input type="submit" value="Continue" class="button large blue" v-show="!loading" v-cloak  />
                </div>
            </div>
        </form>
    </setup>
</template>
