import Vue from "vue";
import VueRouter from "vue-router";
import Login from "../views/Login.vue";
import LoginSSO from "../views/LoginSSO.vue";
import Emails from "../views/Emails.vue";
import Reset from "../views/Reset.vue";
import Recover from "../views/Recover.vue";
import SetupPassword from "../views/SetupPassword.vue";
import SetupQuestions from "../views/SetupQuestions.vue";
import Instructions from "../views/Instructions.vue";
import SetNew from "../views/SetNew.vue";

Vue.use(VueRouter);

const routes = [
    {
        path: "/",
        name: "Login",
        component: Login
    },
    {
        path: "/LoginSSO",
        name: "LoginSSO",
        component: LoginSSO
    },
    {
        path: "/Emails",
        name: "Emails",
        component: Emails
    },
    {
        path: "/Reset",
        name: "Reset",
        component: Reset
    },
    {
        path: "/Recover",
        name: "Recover",
        component: Recover
    },
    {
        path: "/SetupPassword",
        name: "SetupPassword",
        component: SetupPassword
    },
    {
        path: "/SetupQuestions",
        name: "SetupQuestions",
        component: SetupQuestions,
        props: true
    },
    {
        path: "/Instructions",
        name: "Instructions",
        component: Instructions,
        props: true
    },
    {
        path: "/SetNew",
        name: "SetNew",
        component: SetNew,
        props: true
    }
];

const router = new VueRouter({
    mode: "hash",
    base: process.env.BASE_URL,
    routes
});

export default router;
