<script>
	import Setup from "./Setup.vue";
	export default {
		name: "SetupPassword",
		data() {
			return {
				error: "",
				loading: false,
				redirectUrl: "",
				output: {
					password: "",
					confirmPassword: "",
				},
			};
		},
		components: {
			Setup,
		},
		methods: {
			handleFormSubmit() {
				let data = {
					redirectUrl: this.redirectUrl,
					password: this.output.password,
					confirmPassword: this.output.confirmPassword,
				};
				this.error = "";
				if (data.password == "" && data.confirmPassword == "") {
					this.error = "Password fields cannot be blank.";
				} else if (data.confirmPassword == "") {
					this.error = "Confirm Password field cannot be blank.";
				} else if (data.password == "") {
					this.error = "Password field cannot be blank.";
				} else if (data.password != data.confirmPassword) {
					this.error = "Password fields do not match.";
				}
				if (this.error != "") return;

				this.loading = true;

				this.$ajax
					.post("/Setup/Password", data)
					.then((resp) => {
						if (typeof resp.data != "undefined") {
							if (resp.data.Error != null) {
								this.error = resp.data.Error;
							} else if (
								resp.data.ClientRedirect &&
								resp.data.RedirectUrl.length
							) {
								window.location.href = resp.data.RedirectUrl;
							} else if (resp.data.RedirectPage != null) {
								var queryString =
									resp.data.RedirectUrl != null
										? { redirectUrl: resp.data.RedirectUrl }
										: {};
								this.$router.push({
									name: resp.data.RedirectPage,
									query: queryString,
								});
							}
						}
					})
					.catch(() => {})
					.then(() => {
						this.loading = false;
					});
			},
		},
		mounted() {
			// this.$reformatUrl("SetupPassword");
			if (
				typeof this.$route.query != "undefined" &&
				typeof this.$route.query.redirectUrl != "undefined"
			) {
				this.redirectUrl = this.$route.query.redirectUrl;
			}
		},
	};
</script>

<template>
	<setup title="Setup Password" :error="error">
		<form method="post" @submit.prevent="handleFormSubmit">
			<passwordPage :loading="loading" v-model="output"></passwordPage>
		</form>
	</setup>
</template>
